import * as Yup from 'yup'

const numberOnlyReg: RegExp = /^[0-9]*$/

export const validate = Yup.object({
  productName: Yup.string()
    .min(4, '    4 ')
    .max(35, '    35 ')
    .required('    '),
  category: Yup.string()
    .max(50, '    50 ')
    .required('   '),
  description: Yup.string()
    .min(10, '    10 ')
    .max(300, '    300 ')
    .required('    '),
  startPrice: Yup.string()
    .matches(numberOnlyReg, '     ')
    .max(11, '     999,999,999')
    .required('    '),
  priceStep: Yup.string()
    .matches(numberOnlyReg, '      ')
    .max(10, '   10 ')
    .required('    '),
  seePrice: Yup.string()
    .matches(numberOnlyReg, '     ')
    .max(15, '    20 ')
    .required('    '),
  stepTime: Yup.string()
    .matches(numberOnlyReg, '         ')
    .max(10, '  : 10')
    .required('    '),
  percentTimeStep: Yup.string()
    .matches(numberOnlyReg, '   ')
    .max(2, '     0  99')
    .required('    '),
})