import * as Yup from 'yup'

const emailReg: RegExp = /[A-Z0-9._%+-]+@[A-Z0-9-]+.+.[A-Z]{2,4}/igm

export const validate = Yup.object({
  email: Yup.string()
    .matches(emailReg, ' ! Email   : xxx@zzz.cc')
    .required('Email    '),
  login: Yup.string()
      .min(4, '     4 ')
      .max(15, '     15 ')
      .required('    '),
  reg_password: Yup.string()
    .min(6, '     6 ')
    .required(' '),
  confirmPassword: Yup.string()
    .oneOf([Yup.ref('reg_password'), null], '  ')
    .required(' ')
})